'use strict';

// JQUERY  Import Modules

import jquery from 'jquery';

// JQUERY  Init Jquery

window.$ = jquery;
window.jQuery = jquery;

// HOME  Building

export const home = () => {

    let header = $('#header'),
        closeButtonMenu = $('#close-menu'),
        openButtonMenu = $('#open-menu');

    $('body').on('click', '#open-menu', function () {
        $(this).prop('disabled', true);
        $('.mp-menu-b').scrollTop(0);

        closeButtonMenu.prop('disabled', false);
        header.removeClass('closed').addClass('opened');
        $('body').addClass('locked');

    }).on('click', '#close-menu', function () {
        $(this).prop('disabled', true);
        openButtonMenu.prop('disabled', false);
        header.removeClass('opened').addClass('closed');
        $('body').removeClass('locked');
    });
    
};